import React, { useState, useRef } from 'react'
import { Link } from 'react-router-dom'

import './weather.css'

const Weather = () => {

    const [city, setCity] = useState('');
    const [forcast, setForcast] = useState({
        day: '',
        temp: '',
        wind: '',
        short: ''
    });
    const dayRef = useRef();

    const getWeather = async() => {
        dayRef.current.innerText = 'fetching Data...'
        const GEOCODE_URL = `https://nominatim.openstreetmap.org/search?q=${city}&format=json`;
        let forcast;
        try {
            const DATA = await (await fetch(GEOCODE_URL)).json();    
            const LAN_LON_URL = `https://api.weather.gov/points/${DATA[0].lat},${DATA[0].lon}/forecast`;
            forcast = await (await fetch(LAN_LON_URL)).json();
        } catch (error) {
            console.log('something went wrong, try again', error);
        }
        let data = forcast.properties.periods[0];
        setForcast({
            day: data.name,
            temp: `temp: ${data.temperature}`,
            wind: `wind: ${data.windSpeed} ${data.windDirection}`,
            short: `Todays Forcast: ${data.shortForecast}`
        });
    }

    return (
        <div className="weatherContainer">            
            <header className="centerIt">
                <h3>Check the WEATHER</h3>
                <p><em>the US version</em></p>
            </header>
            <div className="searchContainer centerIt">
                <div className="searchInputs">
                    <input 
                        onChange={e => setCity(e.target.value)} 
                        className="city" 
                        type="text" 
                        placeholder="Enter a US city"/>
                </div>
                <button onClick={getWeather}>search</button>
            </div>
            <div className="weatherCenSec centerIt">
                <div className="weatherData">
                    <p ref={dayRef}>{forcast.day}</p>
                    <p>{forcast.temp}</p>
                    <p>{forcast.wind}</p>
                    <p>{forcast.short}</p>
                </div>
            </div>
            <footer className="centerIt">
                <p>created by Or Regev</p>
                <Link to="/projects" className="exitLink">
                    <span>EXIT</span>
                </Link>
            </footer>
          
        </div>
    )
}
export default Weather;