import React, { useContext } from 'react'
import { RoomContext } from '../contexts/RoomContext'
import { useParams } from 'react-router-dom'
import Adddevice from './Adddevice'
import Device from './Device'
import '../styles/Room.css'

const Room = () => { 
    const { roomIndex } = useParams();  
    const [roomList, ] = useContext(RoomContext);
    
    return (
        <div className="roomContainer centerIt">
            <div className="room">
                <div className="roomHead centerIt">
                    <div className="roomColor" style={{backgroundColor: roomList[roomIndex].color}}></div>
                    <h3>{roomList[roomIndex].name}</h3>
                </div>
                <div className="roomType">
                    <p>{roomList[roomIndex].type}</p>
                </div>
                <div className="roomDevices">
                    {roomList[roomIndex].devices.map((device, i) => {
                        return (
                            <Device key={i} deviceIndex={i}/>
                        )
                    })}
                </div>
            </div> 
            <div>
                <Adddevice/>
            </div>
        </div>
    )
}
export default Room;