import React, { useState, useContext } from "react";
import { RoomContext } from "../contexts/RoomContext";
import { useParams } from "react-router-dom";
import "../styles/Device.css";

const Device = (props) => {
	const { roomIndex } = useParams();
	const [deviceIndex] = useState(props.deviceIndex);
	const [roomList, setRoomList] = useContext(RoomContext);
	const [bgColor, setBgColor] = useState(roomList[roomIndex].devices[deviceIndex].status ? "#448d44" : "#c94444");

	const delDevice = () => {
		let tempList = roomList.map((room, index) => {
			if (index === parseInt(roomIndex)) {
				let tempRoom = room.devices.filter((device, i) => i !== deviceIndex);
				room.devices = [...tempRoom];
				return room;
			} else {
				return room;
			}
		});
		setRoomList([...tempList]);
	};

	const toggleDevice = () => {
		let tempList = roomList.map((room, index) => {
			if (index === parseInt(roomIndex)) {
				room.devices[deviceIndex].status = !room.devices[deviceIndex].status;
				setBgColor(room.devices[deviceIndex].status ? "#448d44" : "#c94444");
				return room;
			} else {
				return room;
			}
		});
		setRoomList([...tempList]);
	};

	return (
		<div className="smartDevice" style={{ backgroundColor: bgColor }}>
			<div className="smartDeviceHead">
				<p>{roomList[roomIndex].devices[deviceIndex].type}</p>
			</div>
			<button className="smartOnOffBtn" onClick={toggleDevice}>
				ON / OFF
			</button>
			<button className="removeDeviceBtn centerIt" onClick={delDevice}>
				{/* EXTRACT ICON TO ICON FOLDER AND IMPORT */}
				<svg
					width="1.1em"
					height="1.1em"
					viewBox="0 0 16 16"
					className="bi bi-trash"
					fill="currentColor"
					xmlns="http://www.w3.org/2000/svg"
				>
					<path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z" />
					<path
						fillRule="evenodd"
						d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4L4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z"
					/>
				</svg>
			</button>
		</div>
	);
};
export default Device;
