import React, { useState } from "react";
import Popup from "../popUps/Popup";
import "./level.css";

const Level = ({ handleLevelClick }) => {
	const [popupVis, setPopupVis] = useState(false);

	return (
		<div className="levelContainer centerIt">
			<div className="upperSec centerIt">
				<h3>CHOOSE LEVEL</h3>
			</div>
			<div className="levNav centerSec centerIt">
				<button className="levEasyBtn levBtn" onClick={() => handleLevelClick(0)}>
					EASY
				</button>
				<button className="levMedBtn levBtn" onClick={() => handleLevelClick(1)}>
					MEDIUM
				</button>
				<button className="levHardBtn levBtn" onClick={() => setPopupVis((last) => !last)}>
					HARD
				</button>
				{popupVis && (
					<Popup
						msg="ARE YOU SURE"
						type="level"
						backFunc={() => setPopupVis((last) => !last)}
						forwFunc={() => handleLevelClick(2)}
					/>
				)}
			</div>
		</div>
	);
};
export default Level;
