import React, { useState } from "react";
import Level from "./level/Level";
import Active from "./active/Active";
import { getBoard } from "./database";
import "./game.css";

// NO LIMIT ON NEW GAMES !! (CRUSH WITH BOARD LIMIT)

const Game = () => {
	const N_TIPS = 5;
	const [tipData, setTipData] = useState({ nTips: N_TIPS, btnState: false });
	const [switchLevGame, setSwitchLevGame] = useState(true);
	const [boardIds, setBoardsIds] = useState([]); // The old boards id
	const [boards, setBoards] = useState({}); // The challenge and solution
	const [activeData, setActiveData] = useState([]); // The UI.
	const [currentCell, setCell] = useState(0); // The current cell being played.

	const getSolution = () => {
		let board = getBoard(boardIds);
		setBoardsIds([...boardIds, board.boardID]);
		return board.boardData;
	};

	const getChallange = (level, solutionArr) => {
		const MIN = 1,
			MAX = 4;
		let challangeFromSolution = solutionArr.map((row) =>
			row.map((cell) => {
				//FOR CHECKING AGAINST FULL BOARD (Win condition), FOR TESTS;
				let rand = Math.floor(Math.random() * MAX) + MIN;
				if (level === 0) return rand !== 1 ? cell : null;
				else if (level === 1) return rand === 1 || rand === 2 ? cell : null;
				return rand === 1 ? cell : null;
			})
		);
		return challangeFromSolution;
	};

	const createActiveData = (challangeArr) => {
		let counter = 0;
		let convertedDB = challangeArr.map((row) =>
			row.map((cell) => {
				counter++;
				return { id: counter, data: cell, vis: cell === null, isTip: false };
			})
		);
		setActiveData([...convertedDB]);
	};

	const createData = (level) => {
		let solutionArr = getSolution();
		let challangeArr = getChallange(level, solutionArr);
		setBoards({ solution: solutionArr, challenge: challangeArr });
		createActiveData(challangeArr);
	};

	const handleLevelClick = (level) => {
		createData(level);
		setSwitchLevGame(false);
	};

	const checkBoard = () => {
		for (let i = 0; i < activeData.length; i++) {
			for (let j = 0; j < activeData[i].length; j++) {
				if (activeData[i][j].data === null) {
					return 0; // Not full
				}
			}
		}
		for (let i = 0; i < activeData.length; i++) {
			for (let j = 0; j < activeData[i].length; j++) {
				if (activeData[i][j].data !== boards.solution[i][j]) {
					return 1; // Not Correct
				}
			}
		}
		return 2; // Correct
	};

	const resetGame = () => {
		let newArr = activeData.map((row) =>
			row.map((cell) => {
				return cell.vis || cell.isTip ? { id: cell.id, data: null, vis: true, isTip: false } : cell;
			})
		);
		setTipData({ nTips: N_TIPS, btnState: false });
		setActiveData([...newArr]);
	};

	const newGame = () => {
		setTipData({ nTips: 5, btnState: false });
		setSwitchLevGame(true);
	};

	const giveTip = () => {
		const MIN = 1;
		const MAX = 81;
		let cell = null;
		let pos = { x: null, y: null };

		// Step 3: finds the cell in the UI and updates its position in the solution
		const findCell = (rand) => {
			for (let i = 0; i < activeData.length; i++) {
				for (let j = 0; j < activeData[i].length; j++) {
					if (activeData[i][j].id === rand) {
						if (activeData[i][j].data === null) {
							cell = activeData[i][j]; // The empty cell from the UI.
							pos = { x: i, y: j }; // Position of data in the solution array.
							return true;
						}
					}
				}
			}
			return false;
		};

		while (true) {
			let rand = Math.floor(Math.random() * (MAX - MIN) + MIN); // Step 1: gets a randome cell
			if (findCell(rand)) break; // Step 2: checks if cell is empty
		}

		// Step 4: updates the UI
		let boardWithTip = activeData.map((row) =>
			row.map((cellT) => {
				return cellT.id === cell.id
					? { id: cellT.id, data: boards.solution[pos.x][pos.y], vis: false, isTip: true }
					: cellT;
			})
		);
		setActiveData([...boardWithTip]);
	};

	const handlePedClick = (num) => {
		let newData = activeData.map((row) =>
			row.map((cell) => {
				return currentCell === cell.id ? { id: cell.id, data: num, vis: cell.vis, isTip: cell.isTip } : cell;
			})
		);
		setActiveData([...newData]);
	};

	const handleTipPress = () => {
		if (checkBoard() === 0) {
			giveTip();
			tipData.nTips > 1
				? setTipData({ nTips: tipData.nTips - 1, btnState: tipData.btnState })
				: setTipData({ nTips: 0, btnState: true });
		}
	};

	const componentSwitcher = () => {
		return switchLevGame ? (
			<Level handleLevelClick={handleLevelClick} />
		) : (
			<Active
				activeData={activeData}
				setCell={setCell}
				handlePedClick={handlePedClick}
				checkBoard={checkBoard}
				resetGame={resetGame}
				newGame={newGame}
				handleTipPress={handleTipPress}
				tipData={tipData}
			/>
		);
	};

	return <div className="gameContainer">{componentSwitcher()}</div>;
};
export default Game;
