import React from "react";
import { Link } from "react-router-dom";
import "./sudokuhome.css";
import "./animation.css";

const Sudokuhome = () => {
	const setRootVarsStyleProps = () => {
		const ANIM_MAX_SIZE = 500;
		const WIDTH = window.innerWidth;
		const HEIGHT = window.innerHeight;
		let tempSize = WIDTH < HEIGHT ? WIDTH : HEIGHT;
		const ANIM_SIZE = tempSize > ANIM_MAX_SIZE ? ANIM_MAX_SIZE : tempSize; // Max size for animation
		const MARGIN = ANIM_SIZE * 0.9 + "px";
		const BOX_MARGIN = ANIM_SIZE * 0.9 * 0.3 * 0.2 + "px";
		document.documentElement.style.setProperty("--width", MARGIN);
		document.documentElement.style.setProperty("--height", MARGIN);
		document.documentElement.style.setProperty("--boxWidth", BOX_MARGIN);
		document.documentElement.style.setProperty("--boxHeight", BOX_MARGIN);
	};

	const createAnimation = () => {
		setRootVarsStyleProps();

		const N_SQUARES = 9;
		const N_CELLS = 9;

		// Step 3: create the cells
		const createBox = () => {
			const boxArr = [];
			for (let j = 0; j < N_CELLS; j++) {
				boxArr.push(<div key={j} className="box"></div>);
			}
			return <>{boxArr.map((item) => item)}</>;
		};
		// Step 2: create the region
		const createSq = () => {
			const sqArr = [];
			for (let i = 0; i < N_SQUARES; i++) {
				sqArr.push(
					<div key={i} className={`sq${i} sq animDef`}>
						{createBox()}
					</div>
				);
			}
			return <>{sqArr.map((item) => item)}</>;
		};
		// Step 1: create container
		return <div className="anim">{createSq()}</div>;
	};

	return (
		<div className="sudokuHomeContainer">
			<Link to="/mainmenu" className="linkContainer">
				<div className="upperSec centerIt">
					<h1>SUDOKU</h1>
				</div>
				<div className="animation centerSec centerIt">{createAnimation()}</div>
				<div id="tapLink" className="welcomeBot centerIt">
					<p>
						<em>TAP ANYWHERE</em>
					</p>
				</div>
			</Link>
		</div>
	);
};
export default Sudokuhome;
