import React, { useContext } from "react";
import { Link } from "react-router-dom";
import { RoomContext } from "../contexts/RoomContext";
import Roomsm from "./Roomsm";
import "../styles/Homepage.css";

const Homepage = () => {
	const [roomList] = useContext(RoomContext);

	return (
		<div className="smartHomepageContainer centerIt">
			<div className="smartRoomsContainer centerIt">
				{roomList.length === 0 ? (
					<p>
						Start by
						<br />
						adding a room
					</p>
				) : (
					roomList.map((room, index) => {
						return <Roomsm key={index} index={index} />;
					})
				)}
			</div>

			<div className="btnCont centerIt">
				<Link to="/projects/smarthome/addroom" className="links centerIt">
					<button className="addRoomBtn centerIt">ADD ROOM</button>
				</Link>
			</div>
		</div>
	);
};
export default Homepage;
