import React, { useState, useContext } from "react";
import { RoomContext } from "../contexts/RoomContext";
import { useParams } from "react-router-dom";
import "../styles/Adddevice.css";

const Adddevice = () => {
	const { roomIndex } = useParams();
	const [displayState, setDisplayState] = useState(false);
	const [type, setType] = useState("");
	const [roomList, setRoomList] = useContext(RoomContext);

	const addDevice = () => {
		updateVis();
		const add = () => {
			let tempList = roomList.map((room, index) => {
				if (index === parseInt(roomIndex)) {
					room.devices = [...room.devices, { type: type, status: false }];
					return room;
				} else {
					return room;
				}
			});
			setRoomList([...tempList]);
		};
		if (type !== "" && roomList[roomIndex].devices.length < 5) {
			let haveStereo = roomList[roomIndex].devices.findIndex((device) => device.type === type);
			let isShower = roomList[roomIndex].type === "BATH";
			if (
				(type === "Stereo" && haveStereo === -1) ||
				(type === "Boiler" && isShower) ||
				type === "Lights" ||
				type === "Air con"
			) {
				add();
			} else {
				alert("ERROR");
			}
		} else {
			alert("ERROR");
		}
	};

	const updateVis = () => {
		setDisplayState(!displayState);
	};

	const showScr = () => {
		if (displayState) {
			return (
				<div className="addDeviceConatanier">
					<select onChange={(e) => setType(e.target.value)} placeholder="Choose room type" defaultValue={"DEFAULT"}>
						<option
							/* disabled * preheps it is better to disable it and let the user return
                                   without choosing a new device rather than alerting ! 
                                   => => => disabled <= <= <= */
							value="DEFAULT"
						>
							Select device type
						</option>
						<option>Air con</option>
						<option>Lights</option>
						<option>Stereo</option>
						<option>Boiler</option>
					</select>
					<button onClick={addDevice}>Add</button>
				</div>
			);
		} else {
			return (
				<div className="addDeviceBtnContainer centerIt">
					<button className="addDeviceBtn centerIt" onClick={updateVis}>
						ADD DEVICE
					</button>
				</div>
			);
		}
	};
	return <div className="addDevicesContainer centerIt">{showScr()}</div>;
};
export default Adddevice;
