import React, { useState } from "react";
import Popup from "../../popUps/Popup";
import "./nav.css";

const Nav = ({ checkBoard, resetGame, newGame, handleTipPress, tipData }) => {
	const [chPopup, setChPopup] = useState({ vis: false, msg: "" });
	const [rePopupVis, setRePopupVis] = useState(false);
	const [newPopupVis, setNewPopupVis] = useState(false);

	/* CHECK SECTION */
	const check = () => {
		let result = checkBoard();
		switch (result) {
			case 0: {
				setChPopup({ vis: true, msg: "NOT FULL" });
				break;
			}
			case 1: {
				setChPopup({ vis: true, msg: "NOT CORRECT" });
				break;
			}
			case 2: {
				setChPopup({ vis: true, msg: "CORRECT" });
				break;
			}
			default:
				break;
		}
	};

	return (
		<div className="navContainer centerIt">
			<div className="navTopSec centerIt">
				<button onClick={() => check()}>CHECK</button>
				{chPopup.vis && <Popup msg={chPopup.msg} type="check" backFunc={() => setChPopup((last) => !last)} />}
			</div>
			<div className="navBotSec centerIt">
				<button onClick={() => setRePopupVis((last) => !last)}>RESET</button>
				{rePopupVis && (
					<Popup msg="RESET GAME" type="reset" backFunc={() => setRePopupVis((last) => !last)} forwFunc={resetGame} />
				)}
				<button onClick={() => setNewPopupVis((last) => !last)}>NEW GAME</button>
				{newPopupVis && (
					<Popup msg="START NEW GAME" type="new" backFunc={() => setNewPopupVis((last) => !last)} forwFunc={newGame} />
				)}
				<button
					onClick={() => handleTipPress()}
					disabled={tipData.btnState}
					style={{ color: tipData.btnState ? "gray" : "inherit" }}
				>
					TIP
				</button>
			</div>
		</div>
	);
};
export default Nav;
