import React from "react";
import Region from "./region/Region";
import "./board.css";

const Board = ({ activeData, setCell, gameArea }) => {
	//console.log("render board");
	let boardStyle = {};
	let cellSize = 0;
	let board = [];

	const setBoardSize = () => {
		let size = gameArea.w < gameArea.h ? gameArea.w : gameArea.h;
		boardStyle = { width: size, height: size };

		const reginSize = Math.floor(size / 3) * 0.9;
		cellSize = Math.floor(reginSize / 3) * 0.9;
	};

	/* Converts two dimentional array to corresponding Squares on the UI */
	const convertDataToUI = () => {
		let arrCellIndex = 0; // The Data cell index
		let boardCellIndex = 0; // The UI cell index
		let rowIndex = 0; // Index of row in region
		let rowRepeat = 0; // Updates the row Index to the begining of the region
		let cellIndex = 0; // Index of cell in the Region
		let cellrowRepeat = 0; // Updates the cellIndex Index to the next row of cells

		/* Build index:
		 * i = The Region index, 0-8
		 *
		 * j = The Row index, used both to access the Data array and the UI array
		 *
		 *          J      rowIndex   rowRepeat
		 *      0, 1, 2   -   0           0
		 *      0, 1, 2   -   0           1
		 *      0, 1, 2   -   0           2
		 *      3, 4, 5   -   3           0
		 *      3, 4, 5   -   3           1
		 *      3, 4, 5   -   3           2
		 *      6, 7, 8   -   6           0
		 *      6, 7, 8   -   6           1
		 *      6, 7, 8   -   6           2
		 *
		 */

		for (let i = 0; i < activeData.length; i++) {
			// loops the regions to build on the UI rows
			//console.log("UI Region:", i);
			let region = []; // Builds a region

			for (let j = rowIndex; j < rowIndex + 3; j++) {
				// loops the rows to be filled in the new region
				//console.log("Data Row:", j, "UI Region row:", cellrowRepeat);

				// ************************************************** INTERNAL LOOP ***********************************************
				/*
				 * k = The current cell index in the UI 0-80
				 * cellIndex = Index of cell in the Region, 0-80
				 * boardCellIndex = The UI cell index, 0-80
				 * arrCellIndex = The Data cell index in the row, 0-8
				 * cellrowRepeat = The row of cells in the region 0-2
				 */
				for (let k = cellIndex; k < cellIndex + 3; k++) {
					// loops the cells to build in the UI region
					//console.log("Data cell:", arrCellIndex, "UI Cell:", k);
					region.push(activeData[j][arrCellIndex]); // Adds cells (data) to the region
					boardCellIndex = k; // sets the UI cell index for data Extract to be the current 'working on' cell
					arrCellIndex++; // sets the index of the data in the original array row 0-8
				}
				cellrowRepeat++; // Marks repeated rows in a region 0-2

				// This if updates the Index of the original data and position of the cell in the UI
				//console.log(boardCellIndex);
				if (cellrowRepeat === 3) {
					if (boardCellIndex === 27 || boardCellIndex === 54) {
						cellIndex += 18; // sets the starting working cell.
					} else {
						cellIndex = cellIndex - 18 + 3; // sets the starting working cell.
					}
					cellrowRepeat = 0; // resets the row count for every UI region
				} else {
					arrCellIndex -= 3; // sets the cell-index from the data for every row in the region
					cellIndex += 9; // sets the starting working cell.
				}

				// ************************************************** INTERNAL LOOP ***********************************************
			}

			rowRepeat++; // Marks repeated rows 0 - 2
			if (rowRepeat === 3) {
				rowRepeat = 0; // Resets the repeat rows
				// This if resets the starting point for the row loop after 3 regions.
				if (i < 2) {
					rowIndex = 0; // <= starting point
				} else if (i < 5) {
					rowIndex = 3; // <= starting point
					arrCellIndex = 0; // resets the cell-index from the data for every 3 regions
				} else {
					rowIndex = 6; // <= starting point
					arrCellIndex = 0; // resets the cell-index from the data for every 3 regions
				}
			}

			board.push(region);
		}
	};

	(() => {
		setBoardSize();
		convertDataToUI();
	})();

	return (
		<div
			className="boardContainer"
			style={{
				width: boardStyle.width,
				height: boardStyle.height,
			}}
		>
			{board.map((region, index) => {
				return <Region key={index} data={region} setCell={setCell} cellSize={cellSize} />;
			})}
		</div>
	);
};
export default Board;
