import React from "react";
import CellEmpty from "./cells/CellEmpty";
import CellFull from "./cells/CellFull";
import "./region.css";

const Region = ({ data, setCell, cellSize }) => {
	return (
		<div className="regionContainer">
			{data.map((cell) => {
				return cell.vis ? (
					<CellEmpty key={cell.id} cell={cell} setCell={setCell} cellSize={cellSize} />
				) : (
					<CellFull key={cell.id} cell={cell} cellSize={cellSize} />
				);
			})}
		</div>
	);
};
export default Region;
