import React from "react";
import "./cell.css";

const CellEmpty = ({ cell, setCell, cellSize }) => {
	return (
		<div
			onClick={() => setCell(cell.id)}
			className="cellContainer cellEmpty"
			style={{
				width: cellSize,
				height: cellSize,
			}}
		>
			{cell.data}
		</div>
	);
};
export default CellEmpty;
