import React, { useState, useContext } from "react";
import { RoomContext } from "../contexts/RoomContext";
import { Link } from "react-router-dom";
import "../styles/Roomsm.css";

const Roomsm = (props) => {
	const [roomList, setRoomList] = useContext(RoomContext);
	const [index] = useState(props.index);
	const [name] = useState(roomList[index].name);

	const delRoom = () => {
		let tempList = roomList.filter((room, i) => i !== index);
		setRoomList([...tempList]);
	};

	return (
		<div className="roomsm">
			<div className="roomsmColorContainer centerIt">
				<div
					className="roomsmColor"
					style={{ backgroundColor: roomList[index].color }}
				></div>
			</div>
			<Link
				to={`room/${name}/${index}`}
				className="roomsmLinkContainer centerIt"
			>
				<h3 className="roomsmName">{roomList[index].name}</h3>
			</Link>
			<div className="roomsmRemoveBtnContanier centerIt">
				<button className="roomsmRemoveBtn centerIt" onClick={delRoom}>
					{/* EXTRACT ICON TO ICON FOLDER AND IMPORT */}
					<svg
						width="1.5em"
						height="1.5em"
						viewBox="0 0 16 16"
						className="bi bi-trash"
						fill="currentColor"
						xmlns="http://www.w3.org/2000/svg"
					>
						<path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z" />
						<path
							fillRule="evenodd"
							d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4L4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z"
						/>
					</svg>
				</button>
			</div>
		</div>
	);
};
export default Roomsm;
