import React, { useState, useRef, useLayoutEffect } from "react";
import Info from "./info/Info";
import Board from "./board/Board";
import Numpad from "./numpad/Numpad";
import Nav from "./nav/Nav";
import "./active.css";

const Active = (props) => {
	const gameRef = useRef(null);
	const [gameArea, setDimensions] = useState({ w: 0, h: 0 });

	useLayoutEffect(() => {
		if (gameRef.current) {
			setDimensions({
				w: gameRef.current.offsetWidth,
				h: gameRef.current.offsetHeight,
			});
		}
	}, []);

	return (
		<div className="activeContainer centerIt">
			<div className="activeTopSec">
				<Info tipData={props.tipData} />
			</div>
			<div className="activeCenSec centerIt" ref={gameRef}>
				<Board setCell={props.setCell} activeData={props.activeData} gameArea={gameArea} />
			</div>
			<div className="activeBotSec centerIt">
				<Numpad handlePedClick={props.handlePedClick} />
				<Nav
					checkBoard={props.checkBoard}
					resetGame={props.resetGame}
					newGame={props.newGame}
					handleTipPress={props.handleTipPress}
					tipData={props.tipData}
				/>
			</div>
		</div>
	);
};
export default Active;
