export const getBoard = (boradIds) => {
	let min = boards[0].boardID;
	let max = boards[boards.length - 1].boardID;

	while (true) {
		let rand = Math.floor(Math.random() * max) + min;
		if (boradIds.indexOf(rand) === -1) {
			return boards[rand];
		}
	}
	// return boards[0]
};
