import React from "react";
import "./popup.css";
import Exclemation from "./svg/Exclemation.js";
import Question from "./svg/Question.js";

const Popup = ({ msg, type, backFunc, forwFunc }) => {
	const execute = () => {
		backFunc(); // closes the popUp
		forwFunc(); // executes the next step
	};

	const setIcon = () => {
		return type === "check" ? <Exclemation /> : <Question />;
	};

	const setNav = () => {
		return type === "check" ? (
			<button onClick={backFunc} className="popupBtn">
				BACK
			</button>
		) : (
			<>
				<button onClick={backFunc} className="popupBtn">
					BACK
				</button>
				<button onClick={execute} className="popupBtn">
					GO
				</button>
			</>
		);
	};

	return (
		<div className="popupMask centerIt">
			<div className="popupCon" style={{ borderColor: msg === "CORRECT" ? "green" : "var(--myRed)" }}>
				<div>
					<h3>{msg}</h3>
				</div>
				<div className="popupIcon">{setIcon()}</div>
				<div className="popupNav">{setNav()}</div>
			</div>
		</div>
	);
};
export default Popup;
