import React from "react";
import "./numpad.css";

const Numpad = ({ handlePedClick }) => {
	let numPadArr = [1, 2, 3, 4, 5, 6, 7, 8, 9];
	return (
		<div className="numPad">
			{numPadArr.map((num, index) => {
				return (
					<div key={index} className="pad centerIt" onClick={() => handlePedClick(num)}>
						{num}
					</div>
				);
			})}
		</div>
	);
};
export default Numpad;
