import React from "react";
import { BrowserRouter as Router, Switch, Route, Redirect } from "react-router-dom";
import { RoomProvider } from "./contexts/RoomContext";

import Header from "./components/Header";
import Homepage from "./components/Homepage";
import Aboute from "./components/Aboute";
import Addroom from "./components/Addroom";
import Room from "./components/Room";
import Footer from "./components/Footer";

import "./smart.css";

const Smarthome = () => {
	return (
		<RoomProvider>
			<div className="smarthome">
				<Router>
					<Header />
					<Redirect to="/projects/smarthome/" />
					<Switch>
						<Route exact path="/projects/smarthome/">
							<Homepage />
						</Route>
						<Route exact path="/projects/smarthome/about">
							<Aboute />
						</Route>
						<Route exact path="/projects/smarthome/addroom">
							<Addroom />
						</Route>
						<Route exact path="/projects/smarthome/room/:name/:roomIndex">
							<Room />
						</Route>
					</Switch>
				</Router>
				<Footer />
			</div>
		</RoomProvider>
	);
};
export default Smarthome;
