import React, { useState, useContext } from "react";
import { RoomContext } from "../contexts/RoomContext";
import { Link } from "react-router-dom";
import "../styles/Addroom.css";

const Addroom = () => {
	const [type, setType] = useState("");
	const [name, setName] = useState("");
	const [color, setColor] = useState("#000000");
	const [roomList, setRoomList] = useContext(RoomContext);

	const addRoom = () => {
		if (type !== "" && name.length > 0) {
			setRoomList([...roomList, { type: type, name: name, color: color, devices: [] }]);
		} else if (type === "") {
			alert("Room type was not choosen !");
		} else if (name.length === 0) {
			alert("Room name was not entered !");
		}
	};

	return (
		<div className="smartAddroom">
			<h3>Enter room details</h3>
			<select className="smartTypeSelect" onChange={(e) => setType(e.target.value)} defaultValue={"DEFAULT"}>
				<option disabled value="DEFAULT">
					Select room type
				</option>
				<option>BEDROOM</option>
				<option>BATH</option>
				<option>KITCHEN</option>
			</select>
			<input
				className="smartNameInput"
				type="text"
				maxLength="5"
				/* limited * preheps it is better to limit it and let the user return
                   without choosing a new device rather than alerting! 
                   => => => minLength="1" <= <= <= */
				onChange={(e) => setName(e.target.value)}
				placeholder="Enter room name"
			></input>
			<div className="smartColorChooseContainer">
				<label htmlFor="colorInput">Pick a color: </label>
				<input
					className="smartColorInput"
					type="color"
					value={color}
					onChange={(e) => setColor(e.target.value)}
					placeholder="Enter room color"
				></input>
			</div>
			<Link to="/projects/smarthome/" className="addroomBtnLink links centerIt">
				<button onClick={addRoom}>CREATE</button>
			</Link>
		</div>
	);
};
export default Addroom;
