import React from "react";
import "./cell.css";

const CellFull = ({ cell, cellSize }) => {
	return (
		<div
			className="cellContainer cellFull"
			style={{
				width: cellSize,
				height: cellSize,
				color: cell.isTip ? "green" : "inherit",
				fontWeight: cell.isTip ? "900" : "inherit",
			}}
		>
			{cell.data}
		</div>
	);
};
export default CellFull;
